import * as React from "react"
import Experience from "../components/Experience"
import Layout from "../components/Layout"
import SectionTitle from '../components/SectionTitle'
import Project from "../components/Project"
import * as styles from "../styles/home.module.css"
import { graphql } from "gatsby"
import { getImage } from "gatsby-plugin-image"
import Footer from "../components/Footer"
import Helmet from "react-helmet"

export default function Home({data}) {
  const journey = data.allJourneyJson.nodes
  const projects = data.allMarkdownRemark.nodes
  console.log(projects)

  return (
    <Layout>
      <Helmet>
        <title>Farbod Faramarzi</title>
        <meta name="title" content="Product Designer • Farbod Faramarzi" />
        <meta name="description" content="Product Designer • Farbod Faramarzi" />

        <meta property="og:type" content="website" />
        <meta property="og:url" content="https://fabbe.lol/" />
        <meta property="og:title" content="Product Designer • Farbod Faramarzi" />
        <meta property="og:description" content="Product Designer • Farbod Faramarzi" />

        <meta property="twitter:card" content="summary_large_image" />
        <meta property="twitter:url" content="https://fabbe.lol/" />
        <meta property="twitter:title" content="Farbod Faramarzi" />
        <meta property="twitter:description" content="Product Designer • Farbod Faramarzi" />
      </Helmet>
      <section>
        <div className={ styles.header }>
          <h1 className={ styles.title }>I’m Farbod — product designer and tinkerer of software. Crafting at <a target="_blank" rel="noopener noreferrer" href="http://spotify.design">Spotify</a>.</h1>
        </div>
      </section>
      <section className={ styles.journey }>
      <SectionTitle title="Journey"/>
        {journey.map(journey => (
          <Experience company={journey.company} date={journey.date} desc={journey.desc} key={journey.id}/>
        ))}
      </section>
      <section>
        <SectionTitle title="Selected work"/>
        {projects.map(projects => (
          <Project img={getImage(projects.frontmatter.thumb)} company={projects.frontmatter.company} title={projects.frontmatter.title} desc={projects.rawMarkdownBody} link={projects.frontmatter.link} key={projects.id}/>
        ))}
      </section>
      <footer>
        <SectionTitle title="Elsewhere" />
        <Footer />
      </footer>
    </Layout>
  )
}

export const query = graphql`
query MyQuery {
  allJourneyJson {
    nodes {
      id
      company
      date
      desc
    }
  }
  allMarkdownRemark(sort: {frontmatter: {date: DESC}}) {
    nodes {
      id
      frontmatter {
        company
        title
        link
        thumb {
          childImageSharp {
            gatsbyImageData(
              layout: CONSTRAINED
              placeholder: BLURRED
              formats: [AUTO, WEBP]
            )
          }
        }
      }
      rawMarkdownBody
    }
  }
}
`